@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  background: #F6F7FC;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrap {
  display: flex;
}

.block-header {
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 19px;
    color: #989EB8;
}

.block-wrap {
    display: flex;
    justify-content: space-between;
}

.deposits-block {
    /*display: flex;*/
    width: 100%;
    margin: 40px 75px 0 30px;
}

.data-block {
    height: 335px;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    width: 48.5%;
    background: #FFFFFF;
    overflow: hidden;
}

.block-tabs {
    height: 38px;
    background: #E9EDFE;
    display: flex;
    border-radius: 5px 5px 0px 0px;
}

.block-tab {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 50%;
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    color: #989EB8;
}

.block-tab--active {
    background: #FFFFFF;
    color: #242446;
    border-radius: 5px 0 0 0;
}

.content-block--active {
    width: 72%;
}

.inputs-block {
    padding: 29px 18px;
}

.inputs-block-text {
    display: flex;
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    align-items: center;
    color: #242446;
    width: 100%;
}

.input-block {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.amount-input {
    background: #FFFFFF;
    border: 1px solid #E9EDFE;
    box-sizing: border-box;
    border-radius: 5px;
    width: 90%;
    height: 38px;
    margin-top: 8px;
    padding: 12px;
}

.email-input {
    background: #FFFFFF;
    border: 1px solid #E9EDFE;
    box-sizing: border-box;
    border-radius: 5px;
    width: 100%;
    height: 38px;
    margin-top: 8px;
    padding: 12px;
}

.radio-inputs {
    display: flex;
    flex-direction: column;
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    color: #242446;
}

.radio-input {
    margin-top: 20px;
    display: flex;
    align-items: center;
}

.radio-label {
    margin-left: 8px;
}

.block-button {
    background: #0364FF;
    border-radius: 5px;
    padding: 13px 84px;
    border: none;
    color: #FFFFFF;
    display: flex;
    margin: 30px auto;
    cursor: pointer;
}

.data-block-header {
    background: #0364FF;
    height: 38px;
    border-radius: 5px 5px 0px 0px;
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 19px;
    color: #FFFFFF;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 35px 0 10px;
}

.deposit-item {
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    color: #242446;
    border-bottom: 1px solid #C4C4C4;
    padding: 10px 25px 10px 0px;
    display: flex;
    align-items: center;
    /*justify-content: space-between;*/
}

.deposit-item-row {
    width: 44%;
}

.pagination {
    display: flex;
    justify-content: flex-end;
    padding-right: 10px;
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    color: #989EB8;
    padding-top: 15px;
}

.pagination-button {
    display: flex;
    align-items: center;
    padding-right: 16px;
    cursor: pointer;
}

.pagination-last-button {
    display: flex;
    align-items: center;
    margin-left: 10px;
    cursor: pointer;
}

.deposit-items {
    padding: 0 10px;
    height: 250px;
    overflow: hidden;
}

.side-bar {
    background: #FFFFFF;
    height: 100vh;
    width: 76px;
    box-shadow: 4px 0px 20px rgba(0, 0, 0, 0.1);
    transition: .2s;
}

.side-bar-item-wrap {
    height: 250px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-top: 56px;
}

.side-bar-item {
    /*width: 100%;*/
    height: 48px;
    display: flex;
    /*justify-content: flex-end;*/
    align-items: center;
    cursor: pointer;
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 19px;
    color: #242446;
    padding-left: 30px;
}

.side-bar-item--selected {
    background: #E9EDFE;
    border-right: 2px solid #0364FF;
}

.side-bar-item-name {
    padding-left: 10px;
    /*display: none;*/
    visibility: hidden;
}

.side-bar--active {
    width: 20%;
}

.side-bar--active .side-bar-item-name {
    /*display: block;*/
    visibility: visible;
}

.header {
    width: 100%;
    height: 60px;
    background: #242446;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
    display: flex;
    justify-content: space-between;
}

.user-info {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
    margin-right: 45px;
    cursor: pointer;
}

.user-name {
    color: #FFFFFF;
    font-size: 14px;
    line-height: 16px;
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: normal;
    padding-right: 14px;
}

.dropdown-menu {
    display: none;
}

.dropdown-menu--active {
    position: absolute;
    background: #FFFFFF;
    box-shadow: 0px 0px 10px rgb(31 45 61 / 10%);
    border-radius: 5px;
    display: block;
    width: 130px;
    height: 80px;
    top: 60px;
}

.menu-item {
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    display: flex;
    align-items: center;
    color: #242446;
    padding: 12px 14px;
}

.menu-item:hover {
    background: #E9EDFE;
}

.side-bar-head {
    background: #0364FF;
    width: 76px;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    cursor: pointer;
    transition: .2s;
}

.side-bar-head--active {
    width: 20%;
}

.side-bar-head--active .side-bar-arrow {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    transition: .2s;
}

.side-bar-arrow {
    margin-right: 19px;
}

